import React, { useState } from "react";
import { useBearStore } from "../../store/app.js";
import "./ResultOp.css";
import CTA from "../../../img/Gyne/CTA 1.png";
import img from "../../../img/Gyne/thumb nhiem nam am dao.png";
import arrow from "../../../img/Button_img/mui ten.png";
