import { Suspense, useEffect,useCallback } from "react";
import { Outlet, useLocation } from "react-router-dom";
import { useBearStore } from "./store/app";
import "./Layout.css";
const Layout = () => {
  const { increasePopulation } = useBearStore();
  const location = useLocation(); //  Dng useLocation thay v location trc tip
  const id = location.pathname.split("/").pop();

  useEffect(() => {
    if (id === "skin" || id === "intimate") {
      increasePopulation({ condition: id });
    }
  }, []); //  Thm dependency  trnh warning React

const handleWindowSizeChange = useCallback(() => {
    if (window.parent.document.querySelector('iframe[src*="deco"]')) {
      const mainIframe = window.parent.document.querySelector(
        'iframe[src*="deco"]'
      );
 
      if (document.getElementsByClassName("layout_wrapper").length > 0) {
        const mainContainerHeight = document.getElementsByClassName(
          "layout_wrapper"
        )[0].offsetHeight;
        console.log("Height",mainContainerHeight)
        mainIframe.height = mainContainerHeight;
      } 
    }
  }, []);
 
  useEffect(() => {
    window.addEventListener("resize", handleWindowSizeChange);
    return () => {
      window.removeEventListener("resize", handleWindowSizeChange);
    };
  }, [handleWindowSizeChange]);


  return (
    <div className="layout_wrapper">
      {/* <header className="banner">
       
        <div className="top-nav">
          <div className="top-links">
            <a href="#">V chng ti</a>
            <a href="#">Cu hi thng gp</a>
          </div>
          <div className="search-icon"></div>
        </div>

 
        <div className="main-nav">
          <img src="/logo-left.png" alt="Left Logo" className="left-logo" />

          <nav className="menu">
            <a href="#">Chm sc da thn mnh </a>
            <a href="#">Chm sc vng kn </a>
            <a href="#">Sn phm</a>
          </nav>

          <img src="/bayer-logo.png" alt="Bayer Logo" className="right-logo" />
        </div>
      </header> */}
      <p
        style={{
          color: "#c01b31",
          marginTop: "20px",
          marginLeft: "20px",
          fontSize: 24,
          fontWeight: "bold",
        }}
      >
        Symptom checker
      </p>
      <main>
        <Suspense fallback={<p></p>}>
          <Outlet />
        </Suspense>
      </main>
      <footer className="footer_layout">
        <div className="line_layout"></div>
        <div className="footer_content">
          <p>
            Cng c ny cung cp thng tin  bn tham kho v nhn din tnh
            trng sc khe ca mnh, ng thi a ra gi  v phng php ph
            hp. Tuy nhin, cng c ny khng thay th cho li khuyn, chn on
            hoc iu tr y t chuyn mn.
          </p>

          <p className="highlight_text">
            <strong>
              Chng ti khuyn ngh bn nn tham kho  kin ca nhn vin y t
               m bo chn on chnh xc v iu tr hiu qu.
            </strong>
          </p>
          <p>
            <strong>
              NU BN ANG MANG THAI HOC NGHI NG MNH ANG MANG THAI, VUI LNG
              THAM KHO  KIN BC S.
            </strong>
          </p>
          <p>
            V  chnh xc trong vic nhn din triu chng l u tin hng
            u, cng c s dng hnh nh thc t. Chng ti hiu rng mt s
            hnh nh c th khin bn khng thoi mi, nhng chng c a vo
             h tr bn mt cch tt nht.
          </p>
        </div>
      </footer>
    </div>
  );
};

export default Layout;
