import { useBearStore } from "../../store/app.js";
import { useEffect, useState } from "react";
import "./Result_1.css";
import thumbmain from "../../../img/Result_img/namkechan-1080x630.jpg";
import thumbslide1 from "../../../img/Thumb_Slide/03 slider-viemdatiepxuc.jpg";
import thumbslide2 from "../../../img/Thumb_Slide/03 slider-chambanchan.jpg";
import thumbslide3 from "../../../img/Thumb_Slide/03 slider-chamtodia.jpg";
import Arrow_prev from "../../../img/Button_img/slider icon 2.png";
import Arrow_next from "../../../img/Button_img/slider icon.png";
import Btn_Sol from "../../../img/Button_img/button tim hieu.png";
import arrow from "../../../img/Button_img/mui ten.png";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { questionsData } from "../../Data.js";

const sliderData = [
  {
    title: (
      <p>
        Vim da tip xc<sup style={{ fontSize: 12 }}>1,2</sup>
      </p>
    ),
    description: (
      <ul
        style={{
          margin: "20px",
          textAlign: "justify",
        }}
      >
        <li>
          Vng da , nga, c th r dch <sup style={{ fontSize: 12 }}>1</sup>
          .{" "}
        </li>
        <li>
          Thng xut hin trng khp vi v tr giy dp
          <sup style={{ fontSize: 12 }}>2</sup>.
        </li>
        <li>
          {" "}
          Khng nh hng n vng da gia cc ngn chn
          <sup style={{ fontSize: 12 }}>2</sup>.
        </li>
      </ul>
    ),
    img: thumbslide1,
    ref: (
      <>
        1. Litchman G, Nair PA, Atwater AR, et al. Contact Dermatitis. [Updated
        2022 May 8]. In: StatPearls [Internet]. Treasure Island (FL): StatPearls
        Publishing; 2022 Jan. Available from:{" "}
        <a href="https://www.ncbi.nlm.nih.gov/books/NBK459230/" target="_blank">
          https://www.ncbi.nlm.nih.gov/books/NBK459230/
        </a>
        . 2. Ely JW, Rosenfeld S, Seabury Stone M. Diagnosis and Management of
        Tinea Infections. Am Fam Physician. 2014; 90(10):70210.
      </>
    ),
  },
  {
    title: (
      <p>
        Chm bn chn <sup style={{ fontSize: 12 }}>2</sup>
      </p>
    ),
    description: (
      <ul style={{ margin: "20px" }}>
        <li>Thng gp  ngi c tin s d ng hoc vim da c a.</li>
        <li>Khng nh hng n vng da gia cc ngn chn.</li>
      </ul>
    ),
    ref: (
      <>
        2. Ely JW, Rosenfeld S, Seabury Stone M. Diagnosis and Management of
        Tinea Infections. Am Fam Physician. 2014; 90(10):70210.
      </>
    ),
    img: thumbslide2,
  },
  {
    title: (
      <p>
        Chm t a <sup style={{ fontSize: 12 }}>2</sup>
      </p>
    ),
    description: (
      <ul style={{ margin: "20px" }}>
        <li>Xut hin cc mn nc nh ging ht trn chu.</li>
        <li>Thng nm  cnh bn ca ngn tay v ngn chn.</li>
        <li>C th lan n vng bn tay.</li>
      </ul>
    ),
    ref: (
      <>
        2. Ely JW, Rosenfeld S, Seabury Stone M. Diagnosis and Management of
        Tinea Infections. Am Fam Physician. 2014; 90(10):70210.
      </>
    ),
    img: thumbslide3,
  },
];

function Result3() {
  const dataSelect = useBearStore((state) => state.dataSelect);
  const [isOpen, setIsOpen] = useState(false);

  const toggleDropdown = () => {
    setIsOpen(!isOpen);
  };
  //   const answers = location.state?.answers || [];
  useEffect(() => {
    const handleBeforeUnload = (event) => {
      event.preventDefault();
      event.returnValue =
        "Nu bn tip tc hnh ng ny, tt c d liu hin ti s b mt.";
    };

    window.addEventListener("beforeunload", handleBeforeUnload);

    return () => {
      window.removeEventListener("beforeunload", handleBeforeUnload);
    };
  }, []);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3, //  Hin th 2 slide mi ln
    slidesToScroll: 1,
    autoplay: false,
    autoplaySpeed: 3000,
    arrows: true, //  Hin th nt iu hng mc nh
    prevArrow: <img className="prev-button" src={Arrow_prev} alt="Prev" />,
    nextArrow: <img className="next-button" src={Arrow_next} alt="Next" />,
    responsive: [
      {
        breakpoint: 768, //  Khi mn hnh nh hn 768px (mobile)
        settings: {
          slidesToShow: 1, //  Hin th 1 slide
          slidesToScroll: 1,
          arrows: true, // n nt iu hng trn mobile nu cn
          dots: true, // Hin th chm trn  chuyn slide
        },
      },
    ],
  };
  return (
    <div className="symptom-checker">
      {/* Result Section */}
      <section className="result">
        {" "}
        <div className="section_rght">
          <img
            src={thumbmain}
            alt="Du hiu in hnh ca hm k do vi khun"
          />
        </div>
        <div className="section_left">
          {" "}
          <p className="tilte_sick">C th bn ang gp bnh</p>
          <p className="sick">NM K CHN</p>
          <a
            href="https://www.canesten.com.vn/moi-dieu-can-biet-ve-nam-ke-chan-nam-da-chan"
            target="blank"
          >
            <img src={Btn_Sol} alt="NM K CHN" />{" "}
          </a>
        </div>
      </section>

      {/* Slider Section */}
      <section className="slider">
        <p className="content_1">BN C BIT?</p>
        <p className="content_2" style={{}}>
          S khc bit gia Nm k chn v cc bnh ngoi da tng t:
        </p>

        <div className="slider-container_3">
          <Slider {...settings}>
            {sliderData.map((slide, index) => (
              <div key={index} className="slide">
                <img src={slide.img} alt={slide.title} />
                <p style={{ fontSize: 24, fontWeight: 600, marginTop: 20 }}>
                  {slide.title}
                </p>
                <p style={{ fontWeight: 300 }} className="slide_desc">
                  {slide.description}
                </p>
                <p
                  style={{
                    fontSize: 9,
                    fontWeight: 300,
                    position: "absolute",
                    bottom: 20,
                  }}
                  className="slide_ref"
                >
                  {slide.ref}
                </p>
              </div>
            ))}
          </Slider>
        </div>
      </section>
      <div className="Dropdown_result">
        <div onClick={toggleDropdown} className="Info">
          {isOpen ? (
            <div className="Dropdown_result1">
              <p style={{ background: "#172bc1" }}>
                XEM LI CU TR LI <img src={arrow} alt="NM K CHN" />{" "}
              </p>
            </div>
          ) : (
            <div className="Dropdown_result2">
              <p style={{ background: "#172bc1" }}>
                XEM LI CU TR LI <img src={arrow} alt="NM K CHN" />{" "}
              </p>
            </div>
          )}
        </div>
        <div
          className="Xem_KQ"
          style={{
            backgroundColor: "#fff",
            padding: "0",
            maxHeight: isOpen ? "1200px" : "0",

            transition: "max-height 1.5s ease",
          }}
        >
          {isOpen && (
            <div className="expand_result">
              {dataSelect && dataSelect.length > 1 ? (
                dataSelect.slice(1).map((item, index) => (
                  <div
                    key={index}
                    style={{
                      borderBottom: "1px solid #e0e0e0",
                      padding: "0",
                    }}
                  >
                    <div className="Drop_As">
                      <div className="question_Drop">
                        <span className="Queston_Drop" style={{}}>
                          Cu hi {index + 1}:{" "}
                        </span>
                        <span className="Queston_Drop_Title" style={{}}>
                          {item.questionText || "Khng c d liu"}
                        </span>
                      </div>
                      <div className="Answer_Drop" style={{}}>
                        <span style={{ fontWeight: "normal" }}>
                          <strong>Bn tr li: </strong>
                          {index !== 1 &&
                            questionsData.skin
                              .filter((item_) => item_.id === index + 1)[0]
                              .options.filter((op) =>
                                item?.selectedAnswers.includes(op._id)
                              )
                              .map((item__) => item__?.text)
                              .join(". ")}
                          {index === 1 &&
                            questionsData.skin
                              .filter((item_) => item_.id === index + 1)[0]
                              .options.filter((op) =>
                                item?.selectedAnswers.includes(op._id)
                              )[0]?.text}

                          {/* {questionsData.skin
                            .filter((item_) => item_.id === index + 1)[0]
                            .options.filter(
                              (op) => op._id === item.selectedAnswers.join(",")
                            )[0]?.text || "Cha chn p n"} */}
                        </span>
                      </div>
                    </div>
                  </div>
                ))
              ) : (
                <p style={{ textAlign: "center", padding: "20px" }}>
                  Khng c d liu  hin th.
                </p>
              )}
            </div>
          )}
        </div>
      </div>
      {/* Footer */}
      {/* <footer className="footer">
        <p>
          This is a guide on conditions you may have and recommendations for
          suitable treatments. For more accurate diagnosis, please seek advice
          from your healthcare professional.
        </p>
      </footer> */}
    </div>
  );
}

export default Result3;
